import { z } from 'zod';

// Base Claim Schema
const baseClaimSchema = z.object({
  policyNumber: z.string().min(5, 'Nmero de pliza invlido'),
  personalInfo: z.object({
    firstName: z
      .string()
      .min(2, 'El nombre es requerido')
      .max(50)
      .regex(/^[A-Za-z---\u00C0-\u024F\s'-]+$/, 'El nombre contiene caracteres invlidos'),
    lastName: z
      .string()
      .min(2, 'El apellido es requerido')
      .max(50)
      .regex(/^[A-Za-z---\u00C0-\u024F\s'-]+$/, 'El apellido contiene caracteres invlidos'),
    email: z.string().email('Email invlido'),
    phone: z
      .string()
      .regex(/^(?:9\d{8}|[2-7]\d{6,7})$/, 'Telfono invlido. Use solo dgitos sin prefijo; mvil 9 dgitos, fijo 7-8 dgitos'),
  }),
  incidentDate: z
    .string()
    .refine((date) => new Date(date) <= new Date(), 'La fecha no puede ser futura'),
  description: z
  .string()
  .min(20, 'La descripcin debe tener al menos 20 caracteres')
  .max(1000, 'La descripcin no puede exceder 1000 caracteres')
    .regex(/^[A-Za-z---0-9\s,.\-'"]+$/, 'La descripcin contiene caracteres invlidos'),
});

// Health Claim Schema
export const healthClaimSchema = baseClaimSchema
  .extend({
    insuranceType: z.literal('health'),
    claimType: z.enum(['consultation', 'hospitalization', 'surgery', 'medication']),
    medicalCenter: z.string().min(2, 'El centro mdico es requerido'),
    diagnosis: z.string().optional(),
    totalAmount: z.number().min(1, 'El monto debe ser mayor a 0'),
  });

// Vehicle Claim Schema
const vehicleClaimBaseSchema = baseClaimSchema.extend({
  insuranceType: z.literal('vehicle'),
  claimType: z.enum(['accident', 'theft', 'damage', 'total_loss']),
  vehiclePlate: z.string().min(5, 'Placa invlida'),
  location: z.string().min(5, 'La ubicacin es requerida'),
  policeReport: z.boolean(),
  policeReportNumber: z.string().optional(),
  estimatedDamage: z.number().min(0),
});

export const vehicleClaimSchema = vehicleClaimBaseSchema.refine(
  (data) => {
    if (data.policeReport && !data.policeReportNumber) {
      return false;
    }
    return true;
  },
  {
    message: 'Nmero de reporte policial requerido cuando existe reporte',
    path: ['policeReportNumber'],
  }
);

// Union Claim Schema
export const claimSchema = z.union([
  healthClaimSchema,
  vehicleClaimSchema,
]);

export type ClaimSchemaType = z.infer<typeof claimSchema>;
